<template>
  <section>
    <h1>Your Consumers</h1>
    <p>Manage your consumers!</p>

    <table v-if="consumers.length!==0">
      <caption>Consumer Table</caption>
      <colgroup>
        <col />
        <col />
        <col />
        <col />
        <col />
        <col />
        <col />
      </colgroup>

      <tr>
        <th>Username</th>
        <th>Email</th>
        <th>Name &amp; Surname</th>
        <th>Password</th>
        <th>Edit</th>
        <th>Commit Changes</th>
        <th>Delete</th>
      </tr>

      <tr v-for="counter in consumers.length" :key="counter">
        <td>
          <input type="text" :value="consumers[counter-1].username" readonly />
        </td>
        <td>
          <input type="email" :value="consumers[counter-1].email" readonly />
        </td>
        <td>
          <input type="text" :value="consumers[counter-1].nomeCognome" readonly />
        </td>
        <td>
          <input type="password" :value="consumers[counter-1].password" readonly />
        </td>
        <td>
          <!-- +1 perchÃ© dobbiamo tenere in conto anche l'intestazione della riga della tabella -->
          <button title="edit a consumer" @click="changeConsumer(counter+1)">
            <img src="closed_lock.png" alt="closed_lock" />
          </button>
        </td>
        <td>
          <button title="commit edit changes" @click="commitChanges(counter+1)">
            <img src="commit_changes.png" alt="commit_changes" />
          </button>
        </td>
        <td>
          <button
            title="delete definitively a consumer"
            @click="deleteConsumer(consumers[counter-1].username)"
          >
            <img src="delete_button.png" alt="bin" />
          </button>
        </td>
      </tr>
    </table>
    <p v-else>
      Unfortunately, you don't have any affiliate consumer. Go
      <router-link class="router-link" to="/AddConsumer">here</router-link> to add a new consumer
    </p>
    <!-- {{consumers}} -->
  </section>
</template>

<script scoped>
import Axios from "axios";
export default {
  name: "MyConsumers",
  components: {},
  methods: {
    changeConsumer: function (rowNumber) {
      /*let usernameInputElement = document.querySelector(
        `table tr:nth-child(${rowNumber}) td:nth-child(1) input`
      );*/ /*username can't be changed */
      let emailInputElement = document.querySelector(
        `table tr:nth-of-type(${rowNumber}) td:nth-child(2) input`
      );
      let nomeCognomeInputElement = document.querySelector(
        `table tr:nth-of-type(${rowNumber}) td:nth-child(3) input`
      );
      let passwordInputElement = document.querySelector(
        `table tr:nth-of-type(${rowNumber}) td:nth-child(4) input`
      );

      if (passwordInputElement.readOnly === true) {
        /*changing style*/
        /* username can't be change */
        emailInputElement.style.backgroundColor = "white";
        emailInputElement.style.border = "1px solid black";
        nomeCognomeInputElement.style.backgroundColor = "white";
        nomeCognomeInputElement.style.border = "1px solid black";
        passwordInputElement.type = "text";
        passwordInputElement.style.backgroundColor = "white";
        passwordInputElement.style.border = "1px solid black";
        /*change readble input to false*/
        emailInputElement.readOnly = false;
        nomeCognomeInputElement.readOnly = false;
        passwordInputElement.readOnly = false;
        document.querySelector(
          `table tr:nth-of-type(${rowNumber}) td button img[alt="closed_lock"]`
        ).src = "opened_lock.png";
        document.querySelector(
          `table tr:nth-of-type(${rowNumber}) td button img[alt='closed_lock']`
        ).alt = "opened_lock";
      } else {
        emailInputElement.style.backgroundColor = "inherit";
        emailInputElement.style.borderColor = "rgba(0, 0, 0, 0)";
        nomeCognomeInputElement.style.backgroundColor = "inherit";
        nomeCognomeInputElement.style.borderColor = "rgba(0, 0, 0, 0)";
        passwordInputElement.type = "password";
        passwordInputElement.style.backgroundColor = "inherit";
        passwordInputElement.style.borderColor = "rgba(0, 0, 0, 0)";

        emailInputElement.readOnly = true;
        nomeCognomeInputElement.readOnly = true;
        passwordInputElement.readOnly = true;
        document.querySelector(
          `table tr:nth-of-type(${rowNumber}) td button img[alt="opened_lock"]`
        ).src = "closed_lock.png";
        document.querySelector(
          `table tr:nth-of-type(${rowNumber}) td button img[alt='opened_lock']`
        ).alt = "closed_lock";
      }
    },

    commitChanges: function (rowNumber) {
      /*username is the primary key, we can't change it!*/
      /*let username = document.querySelector(
        `table tr:nth-child(${rowNumber}) td:nth-child(1) input`
      ).value;*/
      let email = document.querySelector(
        `table tr:nth-of-type(${rowNumber}) td:nth-child(2) input`
      ).value;
      let nomeCognome = document.querySelector(
        `table tr:nth-of-type(${rowNumber}) td:nth-child(3) input`
      ).value;
      let password = document.querySelector(
        `table tr:nth-of-type(${rowNumber}) td:nth-child(4) input`
      ).value;

      /*this.consumers[rowNumber - 2].username = username;*/
      this.consumers[rowNumber - 2].email = email;
      this.consumers[rowNumber - 2].nomeCognome = nomeCognome;
      this.consumers[rowNumber - 2].password = password;

      Axios.put(
        "http://192.168.1.223:8080/ProgettoTomCat/api/uploaderarea/consumermanagment",
        this.consumers[rowNumber - 2]
      )
        .then((response) => (this.consumers[rowNumber - 2] = response.data))
        .err((error) => alert("dio porco: " + error));
    },

    deleteConsumer: function (consumer_username) {
      let rowToDelete = event.currentTarget.parentElement.parentElement;
      Axios.delete(
        `${process.env.VUE_APP_APIROOT}uploaderarea/consumermanagment/` +//SISTEMA(?)
          consumer_username
      )
        .then(() => rowToDelete.remove())
        .catch(() => alert("Qualcosa Ã¨ andato storto"));
    },
  },
  data: function () {
    return {
      consumers: [],
    };
  },
  created: function () {
    Axios.get(
      `${process.env.VUE_APP_APIROOT}uploaderarea/consumermanagment`
    )
      .then((response) => (this.consumers = response.data))
      .catch((error) => console.log(error));
  },
};
</script>

<style scoped>
section {
  overflow-y: auto;
  display: block;
  max-height: 93vh;
}

h1 {
  text-align: center;
}

table {
  width: 97%;
  margin-left: auto;
  margin-right: auto;
  border-spacing: 0;
  text-align: center;
  border: 1px solid grey;
  border-top-right-radius: 31px;
  border-top-left-radius: 31px;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
}

table caption {
  caption-side: bottom;
  text-align: right;
}

table * * {
  border: 1px solid grey;
}

table tr {
  height: 50px;
}

table tr:first-of-type {
  background-color: rgb(61, 61, 61);
  color: white;
}

table tr th:first-of-type {
  border-top-left-radius: 30px;
}

table tr th:last-of-type {
  border-top-right-radius: 30px;
}

table tr:last-of-type td:first-of-type {
  border-bottom-left-radius: 5px;
}

table tr:last-of-type td:last-of-type {
  border-bottom-right-radius: 5px;
}

input {
  border: 1px solid whitesmoke;
  border-radius: 5px;
  background-color: inherit;
  font-family: inherit;
  text-align: center;
}

input [type="text"],
input [type="email"],
input [type="password"] {
  border-width: 1px;
}

table colgroup col {
  width: 14%;
}

table colgroup col:nth-of-type(5) {
  width: 12%;
}

button {
  border: none;
  background-color: inherit;
}

img[alt="bin"],
img[alt="closed_lock"],
img[alt="opened_lock"],
img[alt="commit_changes"] {
  height: 30px;
  width: auto;
  border: none;
}

</style>